body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.itemDown {
  background: url('https://cdn1.iconfinder.com/data/icons/universal-icons-set-for-web-and-mobile/100/controls_0-13-512.png') !important;
  padding: 5px !important;
  background-position: right top !important;
  background-repeat: no-repeat !important;
  background-size: 30px !important;
}

.itemUp {
  background: url('https://cdn1.iconfinder.com/data/icons/universal-icons-set-for-web-and-mobile/100/controls_0-12-512.png');
  padding: 5px;
  background-position: right top;
  background-repeat: no-repeat;
  background-size: 30px;
}

.react-sanfona-item {
  margin: 5px;
  padding: 5px;
}

.airQualitygradient h2 {
  padding-left: 9px;
}

.explainApp {
  height: 150px;
  width: 90%;
  margin: 0 auto;
}

.explainApp img {
  width: 30%;
  height: 100%;
  padding: 2px;
}

.table.is-striped tbody tr:not(.is-selected):nth-child(2n):hover,
tbody tr:hover,
tbody th:hover {
  background: #00d1b2;
  cursor: pointer;
  color: #fff;
}

.marker {
  background: lightblue;
  border-radius: 100px;
  width: 20px;
  height: 20px;
}
.hoveredObjectData  {
margin-left: -30px;
}
.hoveredObjectData li {
  list-style-type: none;
  padding: 2px;
}

.info-airQuality {
  background: #ffff;
  border-radius: 10px;
  margin: 10px;
  bottom: 20px;
  position: absolute;
}

.info-rangePanel {
  background: #ffff;
  margin: 20px;
  bottom: 5px;
  width: 400px;
  position: absolute;
  right: 0;
}

.accordion {
  width: 400px;
}

.accordion-item button:hover {
  cursor: pointer;
}

.rating span,
.rating-info span,
.rating-traffic span {
  width: 50%;
  font-weight: 500;
  font-size: 13px;
}

.rating-traffic span {
  width: 33%;
}

.rating span {
  width: 33%;
}

span.insufficient {
  padding-right: 20px;

  padding-left: 20px;
}
span.good {
  text-align: left;
}

span.very-bad {
  text-align: right;
}

span.bad {
  padding-left: 10px;
}

span.middle {
  text-align: center;
}

span.moderate {
  padding-right: 20px;

  padding-left: 20px;
}

.rating-info,
.rating,
.rating-traffic {
  display: flex;
  height: 20px;
}

.panel p {
  padding: 0 10px 10px 10px;
  color: #666;
}

.title {
  font-weight: bold;
  padding-left: 9px;
}

.accordion .accordion-item .title {
  color: #000000;
  padding-left: 8px;
}

.data-hover {
  padding: 4px;
  background: #fff;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.25);
  white-space: nowrap;
  border-radius: 10px;
}

@-webkit-keyframes slideIn {
  0% {
    transform: translateY(-900px);
  }
  100% {
    transform: translate(0);
  }
}

.explainPopup .info-panel {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 200;
  -webkit-animation: slideIn 2s forwards;
  -moz-animation: slideIn 2s forwards;
  animation: slideIn 2s forwards;
}


header h1 {
  text-align: center;
}

.info-panel {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 200;

  .content {
    box-shadow: 0px 0px 5px 0px #000000;
    border-radius: 10px;
  }

  .custom-tooltip {
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.7);
    border: 2px solid #c3c3c3;
    border-radius: 10px;

    span {
      font-weight: bold;
    }
  }



  .direction-pop-up h1 {
    font-size: 34px;
    text-align: center;
  }

  p {
    font-size: 15px;
    font-weight: 400;
  }



  button {
    float: right;
  }

  button:hover {
    cursor: pointer;
  }
  .content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    padding: 20px;
    width: 60%;
  }
}

// LOADING
.loading {
  text-align: center;

  img {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    margin: 1rem;
    vertical-align: middle;
    border-radius: 100%;
  }
}

span {
  font-weight: bold;
  margin-bottom: 40px;
}

.explain-N02 {
  font-weight: 200;
  margin: 0 auto;
  width: 85%;
}

.score p {
  font-size: 16px;
  word-wrap: break-word;
  width: 300px;
}

.react-sanfona-item-body-wrapper p {
  font-size: 12px;
}

span.emoji {
  font-size: xx-large;
}

// .mapboxgl-ctrl-top-left {
