//   right: 0;
//   margin: 10px 10px 10px 10px;
// }

@media only screen and (max-width: 720px) {
  .mapboxgl-ctrl-top-left {
    width: 100%;
  }
  .explainPopup .info-panel {
    display: none;
  }

  .info-panel {

    .content {
      width: 85%;
      top:58%
    }
    
  }

  .mapboxgl-ctrl-top-left .mapboxgl-ctrl {
    margin: 0 auto;
    margin-top: 10px;
    float: initial;
    min-width: 95%;
  }
  .info-airQuality {
    margin: 5px;
    bottom: 30px;
  }
}


